<template>
  <div class="out_wrap">
    <div v-if="mode === 1" class="back_wrap flex_between" style="flex-direction:row-reverse;">
      <el-button v-if="baseInfo.is_update === 0" type="primary" @click="edit">修改信息</el-button>
      <el-button v-else type="primary" plain disabled>审核中...</el-button>
    </div>
    <ZfLayout title="基本信息">
      <el-form ref="ruleForm" :model="baseInfo" :rules="rules" label-width="150px" class="demo-ruleForm">
        <template v-if="userinfo.agency_type === 1">
          <div class="flex_between">
            <el-form-item label="企业名称：" prop="agency_name" class="flex1">
              <el-input v-model="baseInfo.agency_name" type="text" placeholder="请输入企业名称" clearable :disabled="mode == 1" />
            </el-form-item>
            <el-form-item label="企业税号：" prop="duty" class="flex1">
              <el-input v-model="baseInfo.duty" type="text" placeholder="请输入企业税号" clearable :disabled="mode == 1" />
            </el-form-item>
          </div>
          <div class="flex_between">
            <el-form-item label="联系人姓名：" prop="name" class="flex1">
              <el-input v-model="baseInfo.name" type="text" placeholder="请输入联系人姓名" clearable :disabled="mode == 1" />
            </el-form-item>
            <el-form-item label="联系人手机号：" prop="mobile" class="flex1">
              <el-input v-model="baseInfo.mobile" type="text" placeholder="请输入联系人手机号" maxlength="11" clearable :disabled="mode == 1" />
            </el-form-item>
          </div>
          <div class="flex_between">
            <el-form-item label="联系人邮箱：" prop="email" class="flex1">
              <el-input v-model="baseInfo.email" type="text" placeholder="请输入联系人邮箱" clearable :disabled="mode == 1" />
            </el-form-item>
            <el-form-item label="企业法人：" prop="legal" class="flex1">
              <el-input v-model="baseInfo.legal" type="text" placeholder="请输入企业法人" clearable :disabled="mode == 1" />
            </el-form-item>
          </div>
          <div class="flex_between">
            <el-form-item label="企业性质：" prop="nature" class="flex1">
              <el-select v-model="baseInfo.nature" clearable filterable class="w100" placeholder="请选择企业性质" :disabled="mode == 1">
                <el-option v-for="(item) in natureArr" :key="item.id" :label="item.nature" :value="item.id" />
              </el-select>
            </el-form-item>
            <el-form-item label="所属行业：" prop="industry" class="flex1">
              <el-input v-model="baseInfo.industry" type="text" placeholder="请输入所属行业" clearable :disabled="mode == 1" />
            </el-form-item>
          </div>
          <div class="flex_between">
            <el-form-item label="企业地址：" prop="address" class="flex1">
              <el-input v-model="baseInfo.address" type="text" placeholder="请输入企业地址" clearable :disabled="mode == 1" />
            </el-form-item>
            <el-form-item label="企业固话：" prop="tel" class="flex1">
              <el-input v-model="baseInfo.tel" type="text" placeholder="请输入企业固话" clearable :disabled="mode == 1" />
            </el-form-item>
          </div>
        </template>
        <template v-else>
          <div class="flex_between">
            <el-form-item label="企业名称：" prop="agency_name" class="flex1">
              <el-input v-model="baseInfo.agency_name" type="text" placeholder="请输入企业名称" clearable :disabled="mode == 1" />
            </el-form-item>
            <el-form-item label="身份证号：" prop="idcard" class="flex1">
              <el-input v-model="baseInfo.idcard" type="text" placeholder="请输入身份证号" clearable :disabled="mode == 1" />
            </el-form-item>
          </div>
          <div class="flex_between">
            <el-form-item label="联系人手机号：" prop="mobile" class="flex1">
              <el-input v-model="baseInfo.mobile" type="text" placeholder="请输入联系人手机号" maxlength="11" clearable :disabled="mode == 1" />
            </el-form-item>
            <el-form-item label="联系人邮箱：" prop="email" class="flex1">
              <el-input v-model="baseInfo.email" type="text" placeholder="请输入联系人邮箱" clearable :disabled="mode == 1" />
            </el-form-item>
          </div>
        </template>
      </el-form>
    </ZfLayout>
    <ZfLayout v-if="userinfo.agency_type === 1" title="营业执照">
      <div v-if="mode == 1">
        <div v-for="(item, index) in baseInfo.license" :key="index" class="file_item flex">
          <div class="flex1 ellipsis">{{ item.filename || item.file }}</div>
          <div v-if="item.filesize" class="w150">{{ item.filesize }}KB</div>
          <div class="w150">
            <a class="add" @click="previewFile(item)">预览</a>
            <a v-if="item.code" class="add" target="_blank" :href="baseUrl + '/api/downloadFile?file_code=' + item.code">下载</a>
          </div>
        </div>
      </div>
      <div v-else>
        <com-upload :arr="baseInfo.license" :show-time="true" @UploadChange="baseInfo.license = [...$event]" />
      </div>
    </ZfLayout>
    <ZfLayout v-else title="身份证">
      <div v-if="mode == 1">
        <div v-for="(item, index) in baseInfo.idcardinfo" :key="index" class="file_item flex">
          <div class="flex1 ellipsis">{{ item.filename || item.file }}</div>
          <div v-if="item.filesize" class="w150">{{ item.filesize }}KB</div>
          <div class="w150">
            <a class="add" @click="previewFile(item)">预览</a>
            <a v-if="item.code" class="add" target="_blank" :href="baseUrl + '/api/downloadFile?file_code=' + item.code">下载</a>
          </div>
        </div>
      </div>
      <div v-else>
        <com-upload :arr="baseInfo.idcardinfo" :show-time="true" @UploadChange="baseInfo.idcardinfo = [...$event]" />
      </div>
    </ZfLayout>
    <ZfLayout title="渠道协议">
      <div v-if="mode == 1">
        <div v-for="(item, index) in baseInfo.contract" :key="index" class="file_item flex">
          <div class="flex1 ellipsis">{{ item.filename || item.file }}</div>
          <div v-if="item.filesize" class="w150">{{ item.filesize }}KB</div>
          <div class="w150">
            <a class="add" @click="previewFile(item)">预览</a>
            <a v-if="item.code" class="add" target="_blank" :href="baseUrl + '/api/downloadFile?file_code=' + item.code">下载</a>
          </div>
        </div>
      </div>
      <div v-else>
        <com-upload :arr="baseInfo.contract" :show-time="true" @UploadChange="baseInfo.contract = [...$event]" />
      </div>
    </ZfLayout>
    <ZfLayout title="服务费率">
      <rateTable :data-source="baseInfo.rate_list" :field="['rate_config']" />
    </ZfLayout>
    <div v-if="mode == 2" class="center">
      <el-button @click="cancelEdit">取消</el-button>
      <el-button type="primary" @click="save('ruleForm')">保存</el-button>
    </div>
  </div>
</template>

<script>
import ZfLayout from '@/components/ZfLayout/index'
import ComUpload from '@/components/upload'
import rateTable from '../clientManage/components/rateTable'
import { mapActions, mapGetters } from 'vuex'
import { natureList } from '@/api/common'
export default {
  components: {
    ZfLayout,
    ComUpload,
    rateTable
  },
  data() {
    const validateMobile = (rule, value, callback) => {
      if (value !== '' && value !== undefined) {
        if (!/^1[3456789]\d{9}$/.test(value)) {
          callback(new Error('请输入正确的手机号'))
        } else {
          callback()
        }
      } else {
        callback()
      }
    }
    const validateEmail = (rule, value, callback) => {
      if (value !== '' && value !== undefined) {
        if (!/^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((.[a-zA-Z0-9_-]{2,3}){1,2})$/.test(value)) {
          callback(new Error('请输入正确的邮箱'))
        } else {
          callback()
        }
      } else {
        callback()
      }
    }
    const validateIdcard = (rule, value, callback) => {
      if (value !== '' && value !== undefined) {
        if (value.length !== 18) {
          callback(new Error('请输入18位身份证号'))
        } else {
          callback()
        }
      } else {
        callback(new Error('请输入身份证号'))
      }
    }
    return {
      baseUrl: process.env.VUE_APP_BASE_API,
      mode: 1,
      rules: {
        agency_name: [{ required: true, message: '请输入企业名称', trigger: 'blur' }],
        duty: [{ required: true, message: '请输入企业名称', trigger: 'blur' }],
        idcard: [{ required: true, validator: validateIdcard, trigger: 'blur' }],
        legal: [{ required: true, message: '请输入企业法人', trigger: 'blur' }],
        mobile: [{ validator: validateMobile, trigger: 'blur', required: true }],
        email: [{ validator: validateEmail, trigger: 'blur', required: true }],
        name: [{ required: true, message: '请输入联系人', trigger: 'blur' }],
        nature: [{ required: true, message: '请输入企业性质', trigger: 'blur' }],
        industry: [{ required: true, message: '请输入企业所属行业', trigger: 'blur' }],
        address: [{ required: true, message: '请输入企业地址', trigger: 'blur' }],
        tel: [{ required: true, message: '请输入企业固话', trigger: 'blur' }]
      },
      natureArr: [],
      rateList: []
    }
  },
  computed: {
    ...mapGetters('accountInfo', ['baseInfo']),
    ...mapGetters(['userinfo'])
  },
  async mounted() {
    natureList().then(res => {
      if (res.code === 200) {
        this.natureArr = res.data
      }
    })
    this.agentInformation()
  },
  methods: {
    ...mapActions('accountInfo', ['agentInformation', 'modifyInfo']),
    previewFile(item) {
      window.open(`${process.env.VUE_APP_IMAGEVIEW}/onlinePreview?url=${item.fullpath || item.file || item.contract}`)
    },
    edit() {
      this.mode = 2
    },
    cancelEdit() {
      this.mode = 1
    },
    save(formName) {
      if (this.userinfo.agency_type === 1 && !this.baseInfo.license.length) {
        this.$message('请上传营业执照')
        return false
      }
      if (this.userinfo.agency_type === 2 && !this.baseInfo.idcardinfo.length) {
        this.$message('请上传法人身份证')
        return false
      }
      if (!this.baseInfo.contract.length) {
        this.$message('请上传业务合同')
        return false
      }
      const query = {
        ...this.baseInfo,
        license: [...this.baseInfo.license.map(d => d.code).filter(d => d)],
        contract: [...this.baseInfo.contract.map(d => d.code).filter(d => d)],
        idcardinfo: [...this.baseInfo.idcardinfo.map(d => d.code).filter(d => d)]
      }
      this.$refs[formName].validate(async(valid) => {
        if (valid) {
          const res = await this.modifyInfo(query)
          if (res.code === 200) {
            this.$message.success('提交成功,等待审核')
            this.agentInformation()
            this.mode = 1
          }
        } else {
          this.$message('请完善必填信息')
        }
      })
    }
  }
}
</script>

<style></style>
